@import "../../style.import.scss";

.search {
    &-title {
        text-align: center;
    }

    &-find-block {
        width: 100%;
        margin-bottom: 10px;

        display: flex;
        flex-direction: column;
        align-items: center;

        input {
            font-size: 19px;
            text-align: center;
            border: none;
            border-radius: $main-border;
            background-color: rgb(231, 231, 231);
            color: black;

            padding: 12px 6px;
            width: 650px;

            &::placeholder {
                opacity: 1;

                color: gray;
            }
        }

        button {
            background-color: $main-color;
            border: none;
            border-radius: $main-border;
            font-size: 18px;
            color: white;

            padding: 5px 7px;
            margin-top: 5px;
        }
    }

    &__item {
        display: flex;
    }

    &__block {
        border: 1px solid $main-color;
        border-radius: $main-border;

        padding: 6px;
    }

    &-accordion {
        &-title {
            margin: 7px 0;
        }

        &__item {
            margin-bottom: 10px;

            border: 1px solid $main-color;
        }

        &__heading {
            background-color: $main-color;
        }

        &__button {
            padding: 7px;

            font-size: 19px;
            color: white;
        }

        &__panel {
            padding: 5px 10px;
        }

        &__products {
            border-top: 2px dashed $main-color;

            margin-top: 5px;
            padding-top: 5px;
        }

        &__product {
            display: flex;

            &:not(&:last-child) {
                margin-bottom: 5px;
            }
        }

        &__status {
            color: orange;
        }
    }
}

@media (max-width: 768px) {
    .search {
        &__block{
            h2{
                text-align: center;

                margin-bottom: 10px;
            }
        }

        &__item{
            justify-content: center;

            border-top: 2px dashed $main-color;

            padding-top: 10px;
        }

        &-title {
            font-size: 25px;
        }

        h3 {
            text-align: center;
        }

        &-find-block {
            input {
                width: 100%;

                font-size: 17px;
            }
        }

        &-accordion {
            &__button{
                font-size: 16px;
                text-align: center;
            }

            &__product {
                display: flex;
                justify-content: center;
            }

            &__panel {
                h3 {
                    font-size: 16px;
                }
            }
        }
    }
}